/**
 * @Author:      zhanghq
 * @DateTime:    2017-12-06 12:44:19
 * @Description: 区县地图数据
 * @Last Modified By:   zhanghq
 * @Last Modified Time:    2017-12-06 12:44:19
 */
export default {
  url: '/main/heatMap/:areaId',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'heatMap': {
        'total|1-10000': 1,
        'curArea': '@province',
        'range': ['10W', '1千'],
        'areaMap|17': [
          {
            'name|+1': ['秀山', '石柱', '彭水', '酉阳', '巫溪县', '巫山县', '开县', '云阳县', '万州'],
            'id|+1': ['500241', '500240', '500243', '500242', '500238', 
              '500237', '500234', '500235', '500236', '500233', '500232',
              '500102', '500110', '500111', '500118', '500117', '500116', '500101'],
            'value|1-1000': 1 ,
            'operator|3': [
              {
                'name|+1': ['电信', '移动', '联通'],
                'value|1-1000': 1,
                'rate|10-100': 1
              }
            ]

          }
        ],
        'mainMap|7': [
          {
            'name|+1': ['渝北区', '江北区', '沙坪坝'],
            'id|+1': ['500112', '500109', '500107', '500113', '500106', 
              '500105', '500108'],
            'value|1-1000': 1 ,
            'operator|3': [
              {
                'name|+1': ['电信', '移动', '联通'],
                'value|1-1000': 1,
                'rate|10-100': 1
              }
            ]
          }
        ]
      }
    }
  }
}



