import $ from 'jquery'

export default class AddAreaName {

  /**
   * Creates an instance of AddAreaName
   * @param {string} selector 容器元素选择器
   * @param {object} opt 图表组件配置项
   */
  constructor(selector, opt) {
    this.config = opt
    const { width, height } = this.config  
    const canvas = document.createElement('canvas')
    canvas.width = width
    canvas.height = height
    canvas.setAttribute('class', 'names-canvas')
    this.canvas = canvas
    $(selector).append(canvas)
    this.cxt = canvas.getContext('2d')
    this.cxt.font = '26px 微软雅黑'
    this.cxt.lineWidth = 1
    this.cxt.shadowOffsetX = 5
    this.cxt.shadowOffsetY = 5
    this.cxt.shadowColor = '#060c31'
    this.cxt.shadowBlur = 10
    this.cxt.strokeStyle = '#1866cc'
    // canvas.addEventListener('mousedown', this.doMouseDown, false)
  }

  getEventPosition(ev){  
    let x, y 
    if (ev.layerX || ev.layerX === 0) {  
      x = ev.layerX 
      y = ev.layerY 
    } else if (ev.offsetX || ev.offsetX === 0) { // Opera  
      x = ev.offsetX 
      y = ev.offsetY 
    }  
    return {x: x, y: y} 
  }  

  /**
   *  渲染地区名字
   *  @param    {object}  data 区域数据
   *  @return   {void}  
   */
  render(data) {
    const self = this
    const cxt = self.cxt
    // 渲染矩形框及文字
    for(let key in data) {
      let center = data[key]
      center[0] = center[0] - 30
      cxt.fillStyle = 'rgba(17, 27, 142, 0.6)'
      let text = key.slice(0, 2)
      let len = cxt.measureText(text).width + 40
      cxt.rect(center[0] - 20, center[1] - 32, len, 45)
      cxt.fill()
      cxt.stroke()
      // cxt.strokeRect(center[0] - 20, center[1] - 32, len, 45)
      cxt.fillStyle = '#fff'
      cxt.fillText(text, center[0], center[1])  
    }

    self.canvas.addEventListener('click', function(e){  
      let p = windowToCanvas(e)  
      console.log(p.x, p.y)
      console.log(self.cxt.isPointInPath(p.x, p.y))
      if(self.cxt.isPointInPath(p.x, p.y)){  
       console.log(p)
      }  
    })  
  }

  /**
   *  渲染主城(大地图)
   *  @param    {object}  data 区域数据
   *  @return   {void}  
   */
  renderMains(data) {
    const self = this
    const cxt = self.cxt
    // 渲染矩形框及文字
    for(let key in data) {
      let center = data[key]
      center[0] = center[0] - 30
      cxt.fillStyle = 'rgba(17, 27, 142, 0.6)'
      let text = key.slice(0, 4)
      let len = cxt.measureText(text).width + 40
      cxt.fillRect(center[0] - 20, center[1] - 32, len, 45)
      cxt.strokeRect(center[0] - 20, center[1] - 32, len, 45)
      cxt.fillStyle = '#fff'
      cxt.fillText(text, center[0], center[1])  
    }
    cxt.isPointInPath(1850, 1630)
  }

  /**
   *  渲染主城(一小块)
   *  @param    {object}  data 区域数据
   *  @return   {void}  
   */
  renderMain(data) {
    const self = this
    const cxt = self.cxt
    cxt.font = '36px 微软雅黑'
    for(let key in data) {
      let center = data[key]
      center[0] = center[0] - 30
      cxt.fillStyle = 'rgba(17, 27, 142, 0.6)'
      let len = cxt.measureText(key).width + 40
      cxt.fillRect(center[0] - 55, center[1] - 45, len, 65)
      cxt.strokeRect(center[0] - 55, center[1] - 45, len, 65)
      cxt.fillStyle = '#fff'
      cxt.fillText(key, center[0] - 30, center[1])  
    }
  }
}



