import $ from 'jquery'
import { fetch, fetchInterval } from '@/util/request'
import loader from '@/loader/base.config.js'
import inNetworkHbs from '../../templates/main/inNetwork.hbs'
import listHbs from '../../templates/common/dropDownList.hbs'
import controlDettialHbs from '../../templates/main/controlDettial.hbs'
import topList from '../../templates/common/controlList.hbs'

// 数字滚动
import DigitRoll from '@/loader/common/scripts/digitroll.js'
import { setWidth, randomNumber } from '../common/common.js'
// 弹窗class
const container = $('.control-detials-dialog')
// 模块名
const MAIN_MODULE = loader.MAIN_MODULE

export default class InNetwork {
  /**
   *  构造函数
   *  @return   {object}  null
   */
  constructor() {
    $(`${MAIN_MODULE} .in-network`).html(inNetworkHbs())
    container.html(controlDettialHbs())
    this.interval = null
  }

  /**
   *  渲染各模块组件
   *  @return   {object}  null
   */
  render() {
    const self = this
    // 10分钟更新一次 请求全市在网终端、在网人数数据
    fetchInterval('fetchInNetwork', (data) => {
      let inNetworkData = data.inNetwork
      // 改变全市在网人数终端number元素的宽
      setWidth(`${MAIN_MODULE} .terminal`, inNetworkData.terminal, 68)
      // 改变管控区域人数number元素的宽
      setWidth(`${MAIN_MODULE} .peopleNumer`, inNetworkData.controlArea, 68)
      // 调用数字滚动
      self.numberRoll(inNetworkData)
      // 事件绑定
      self.bindEvent(data)
    }, loader.updateTime)
  }

  /**
   *  事件绑定
   *  @param    {object}  data 数据
   *  @return   {void}   
   */
  bindEvent(data) {
    const self = this
    let clickNum = 0
    let clickNum2 = 0 // 区域管控人数
    // 下拉列表父级class
    const dropDown = $('.drop-down-list')
    // 全市在网终端鼠标事件 单击显示通讯技术规格
    $(`${MAIN_MODULE} .terminal .number`).on('click', () => {
      data.length = data.operator.length
      let netData = [...data.operator, ...data.communication]
     // 在网终端 flag = 1 与总览页共有一个hbs
      netData.flag = 1 
      if(clickNum % 2 === 0) {
        dropDown.html(listHbs(netData)).fadeIn().css('left', 20) 
      }else{
        dropDown.html('').fadeOut()
      }
      clickNum++
    })

    // 管控区域人数
    $('.peopleNumer .number').on('click', function(evt) {
      data.length = data.areaClassifyCount.length
      data.areaClassifyCount.flag = 2
      const $this = $(evt.target)
      $this.css('cursor', 'pointer')
      if(clickNum2 % 2 === 0) {
        $('.drop-down-list')
          .html(listHbs(data.areaClassifyCount))
          .css('left', 656) 
          .fadeIn()
        // append查看全部
        if(data.areaClassifyCount.length !== 0){
          dropDown.find('.list-container')
            .addClass('control-drop')
            .append('<div class="control-all-btn">查看全部 +</div>')  
        }  
      }else{
        dropDown.html('').fadeOut()
      }
      clickNum2++
    })
 
    // 查看全部
    dropDown.on('click', '.control-all-btn', () => {
      clickNum2 = 0
      $('.control-text').val('').attr('placeholder', '请输入区域名称进行搜索排行')
      fetch('fetchControlDetails', (controlData) => {
        dropDown.fadeOut() // 隐藏下拉列表框
        container.fadeIn().find('.search-list')
          .html(topList(controlData.controlAreaDetails))
      })
    })

    // 点击关闭
    container.on('click', '.close', () => {
      container.hide()
    })

    // 点击搜索
    $('.control-search').on('click', () => {
      $('.search-list').scrollTop(0)
      self.renderSearch()
    })

    // 获取input框的keyCode(回车搜索)
    $('.control-text').on('keypress', (evt) => {
      let keyCode = evt.keyCode
      // 回车
      if(keyCode === 13){
        $('.search-list').scrollTop(0)
        self.renderSearch()
      }
    })

    // 下拉框隐藏-------------------------------

    // 移开在网终端隐藏
    $(`${MAIN_MODULE} .terminal .number`).on('mouseout', () => {
      dropDown.html('').fadeOut()
      clickNum = 0
      clickNum2 = 0
    })

    // 离开下拉列表隐藏
    $('.drop-down-list').hover((evt) => {
      const $this = $(evt.target)
      $this.fadeIn()
    }, () => {
      clickNum = 0
      clickNum2 = 0
      $('.drop-down-list').fadeOut()
    })

    // 点击空白处关闭下拉列表框
    $(document).mouseup(function(e){
      var _con = $('.peopleNumer') || $('.peopleNumer2') // 设置目标区域
      if(!_con.is(e.target) && _con.has(e.target).length === 0){ // Mark 1
        clickNum2 = 0
        $('.drop-down-list').hide()
      }
    })
  }

  /**
   *  渲染搜索
   *  @return   {object}  null
   */
  renderSearch() {
    let text = $('.control-text').val()
    $('.control-text').attr('value', text)
    if(text === '') {
      text = 'all' // 为空搜索全部
    }
    // 发送搜索请求
    fetch('fetchControlSearch', {text}, (data) => {
      $('.search-list').html(topList(data.searchList))
    })
  }

  /**
   *  数字滚动
   *  @param    {object}  data 数据
   *  @return   {object}  null
   */
  numberRoll(data) {
    const self = this
    // 在网终端数据
    let terminal = data.terminal 
    // 管控区域人数 数据
    let controlArea = data.controlArea  
    /**
     *  数字滚动实例化
     */
    // 在网终端 container(值改变要重新添加num)
    const roll1 = new DigitRoll({
      container: `${MAIN_MODULE} .terminal .num-roll` 
    })
    // 管控区域人数 container
    const roll2 = new DigitRoll({
      container:`${MAIN_MODULE} .peopleNumer .num-roll`
    })
    // 在网终端
    roll1.roll(terminal)
    // 管控区域人数
    roll2.roll(controlArea)
    // 变化范围
    let range = 10
    // 清除定时切换
    clearInterval(self.interval)
    // 定时滚动数字
    self.interval = setInterval(() => {
      // 在网终端 (!=0 || null || undefined)
      if(terminal){
        // 真实值1-10范围内随机加减
        let rollTerminal = terminal - range + randomNumber(1, 10)
        roll1.roll(rollTerminal)
      }
      // 管控区域人数(!=0 || null || undefined)
      if(controlArea){
        // 真实值1-10范围内随机加减
        let rollControlArea = controlArea - range + randomNumber(1, 10) 
        roll2.roll(rollControlArea)
      }
      
    }, loader.aniTime) 
  }
}



