import $ from 'jquery'
import loader from '@/loader/base.config.js'
import inNetworkHbs from '../../templates/county/inNetwork.hbs'
// 数字滚动
import DigitRoll from '@/loader/common/scripts/digitroll.js'
import { setWidth, randomNumber } from '../common/common.js'
// 点击展开列表
import listHbs from '../../templates/common/dropDownList.hbs'

// 模块名
const COUNTY_MODULE = loader.COUNTY_MODULE
 
export default class InNetwork {
  /**
   *  构造函数
   *  @return   {void} 
   */
  constructor() {
    $(`${COUNTY_MODULE} .in-network`).html(inNetworkHbs())
    this.interval = null
  }

  /**
   *  渲染各模块组件
   *  @param    {object}  data 数据
   *  @return   {void} 
   */
  render(data) {
    const self = this
    setWidth(`${COUNTY_MODULE} .terminal`, data.inNetwork, 74)
    self.numberRoll(data.inNetwork)
    self.bindEvent(data)
  }

  bindEvent(data) {
    // 合并运营商与网络的数据
    let netData = [...data.operator, ...data.communication]
    // 在网终端 flag = 1 与总览页共有一个hbs
    netData.flag = 1 
    // const self = this
    // 下拉列表父级class
    let clickNum = 0
    const dropDown = $('.drop-down-list')
    // 全市在网终端鼠标事件 单击显示通讯技术规格
    $(`${COUNTY_MODULE} .terminal .number`).on('click', () => {
      if(clickNum % 2 === 0) {
        // data.operator 真实数据从data取
        dropDown.html(listHbs(netData)).fadeIn().css('left', 20) 
      }else{
        dropDown.html('').fadeOut()
      }
      clickNum++
    })

    // 鼠标移开隐藏
    $(`${COUNTY_MODULE} .terminal`).on('mouseout', () => {
      dropDown.html('').fadeOut()
      clickNum = 0
    })
  }

  /**
   *  数字滚动
   *  @param    {object}  data 数据
   *  @return   {void} 
   */
  numberRoll(data) {  
    const self = this 
    // 在网终端 数字滚动实例化
    const roll = new DigitRoll({
      container:`${COUNTY_MODULE} .terminal .num-roll`
    })
    // 在网终端
    roll.roll(data)
    // 变化范围
    let range = 10
    // 清除定时切换
    clearInterval(self.interval)
    // 定时滚动数字
    self.interval = setInterval(() => {
      // 在网终端 (!=0 || null || undefined)
      if(data){
        // 真实值1-10范围内随机加减
        let rollTerminal = data - range + randomNumber(1, 10)
        roll.roll(rollTerminal)
      }     
    }, loader.aniTime) 
  }
}



